.section-advantages{
    background-color:var(--brand-color-pink-background) ;
}
.container-advantages {
    padding-top: 40px;
    padding-bottom: 189px;
  }

  .section-advantages__item{
    list-style: none;
  }
  
  .section-advantages__point {
    font-family: 'Titan One';
    font-style: normal;
    font-weight: 400;
    font-size: 36px;
    line-height: 41px;
    letter-spacing: 0.04em;
    text-transform: uppercase;
  
    padding-top: 16px;
    color: var(--brand-color-red);
    &:hover,
    &:focus {
      transform: rotate(-5deg);
      animation: glow 2s ease-in-out infinite alternate;
    }
  }
  
  .section-advantages__text {
    font-family: 'DM Sans';
    font-style: normal;
    font-weight: 700;
    font-size: 12px;
    line-height: 170%;
    width: 230px;
    padding-top: 20px;
    // padding-bottom: 32px;
    /* or 20px */
  
    color: var(--black-text-color);
  }
  
  .item__milk::before {
    content: url(../images/mobile/mobile-cow.png);
    display: inline-block;
    width: 33px;
    height: 50px;
    animation: reduсe 5s ease-in-out infinite;
  }
  
  @media screen and (min-device-pixel-ratio: 2),
    screen and (min-resolution: 192dpi),
    screen and (min-resolution: 2dppx) {
    .item__milk::before {
      content: url(../images/mobile/mobile-cow@2x.png);
    }
  }
  
  .item__fruit::before {
    content: url(../images/mobile/mobile-cup.png);
    display: inline-block;
    width: 50px;
    height: 50px;
    animation: reduсe 5s ease-in-out infinite;
  }
  @media screen and (min-device-pixel-ratio: 2),
    screen and (min-resolution: 192dpi),
    screen and (min-resolution: 2dppx) {
    .item__fruit::before {
      content: url(../images/mobile/mobile-cup@2x.png);
    }
  }
  .item__icecream::before {
    content: url(../images/mobile/mobile-heart.png);
    display: inline-block;
    width: 44px;
    height: 50px;
    animation: reduсe 5s ease-in-out infinite;
  }
  @media screen and (min-device-pixel-ratio: 2),
    screen and (min-resolution: 192dpi),
    screen and (min-resolution: 2dppx) {
    .item__icecream::before {
      content: url(../images/mobile/mobile-heart@2x.png);
    }
  }
  @keyframes reduсe {
    0% {
      transform: scale(1);
    }
  
    50% {
      transform: scale(0.5);
    }
  
    100% {
      transform: scale(1);
    }
  }
  @media screen and (min-width: 768px) {
    .section-advantages {
      padding-bottom: 33px;
      padding-top: 0;
    }
    .section-advantages__list {
      display: grid;
      grid-template-columns: 180px 180px 180px;
      grid-gap: 25px;
    }
    .section-advantages__text {
      width: 180px;
      font-size: 14px;
      line-height: 174%;
    }
    .section-advantages__point {
      font-size: 48px;
      line-height: 55px;
      padding-top: 36px;
    }
    .item__milk::before {
      content: url(../images/tablet/tablet-cow.png);
      width: 33px;
      height: 50px;
    }
    @media screen and (min-device-pixel-ratio: 2),
      screen and (min-resolution: 192dpi),
      screen and (min-resolution: 2dppx) {
      .item__milk::before {
        content: url(../images/tablet/tablet-cow@2x.png);
      }
    }
  
    .item__fruit::before {
      content: url(../images/tablet/tablet-cup.png);
      width: 50px;
      height: 50px;
    }
    @media screen and (min-device-pixel-ratio: 2),
      screen and (min-resolution: 192dpi),
      screen and (min-resolution: 2dppx) {
      .item__fruit::before {
        content: url(../images/tablet/tablet-cup@2x.png);
      }
    }
    .item__icecream::before {
      content: url(../images/tablet/tablet-heart.png);
      width: 44px;
      height: 50px;
    }
    @media screen and (min-device-pixel-ratio: 2),
      screen and (min-resolution: 192dpi),
      screen and (min-resolution: 2dppx) {
      .item__icecream::before {
        content: url(../images/tablet/tablet-heart@2x.png);
      }
    }
  }
  
  @media screen and (min-width: 1200px) {
    .section-advantages {
      padding-bottom: 100px;
      padding-top: 40px;
    }
    .section-advantages__list {
      display: grid;
      grid-template-columns: 290px 290px 350px;
      grid-gap: 115px;
    }
    .section-advantages__text {
      font-size: 16px;
      line-height: 176%;
      /* or 28px */
      width: 350px;
      letter-spacing: 0.02em;
    }
    .section-advantages__point {
      font-size: 58px;
      line-height: 66px;
      padding-top: 40px;
    }
    .item__milk::before {
      content: url(../images/laptop/laptop-cow.png);
      width: 33px;
      height: 50px;
    }
    @media screen and (min-device-pixel-ratio: 2),
      screen and (min-resolution: 192dpi),
      screen and (min-resolution: 2dppx) {
      .item__milk::before {
        content: url(../images/laptop/laptop-cow@2x.png);
      }
    }
  
    .item__fruit::before {
      content: url(../images/laptop/laptop-cup.png);
      width: 50px;
      height: 50px;
    }
    @media screen and (min-device-pixel-ratio: 2),
      screen and (min-resolution: 192dpi),
      screen and (min-resolution: 2dppx) {
      .item__fruit::before {
        content: url(../images/laptop/laptop-cup@2x.png);
      }
    }
    .item__icecream::before {
      content: url(../images/laptop/laptop-heart.png);
      width: 44px;
      height: 50px;
    }
    @media screen and (min-device-pixel-ratio: 2),
      screen and (min-resolution: 192dpi),
      screen and (min-resolution: 2dppx) {
      .item__icecream::before {
        content: url(../images/laptop/laptop-heart@2x.png);
      }
    }
  }
  